import {HTMLApi} from './HTML';
const IMAGE_EXTENSIONS = ['jpg', 'jpeg', 'png'];
const VIDEO_EXTENSIONS = ['mp4', 'ogv', 'ogg'];
const JS_EXTENSIONS = ['js'];
import _template from 'lodash/template';

// @ts-ignore
// import CSS_BASE from '!!raw-loader!../../self_contained/templates/base.css';
const CSS_BASE = `
html,
body,
.polygonjs-app {
	margin: 0px;
	height: 100%;
	overflow: hidden;
	display: block;
}
.polygonjs-app canvas {
	display: block;
	height: 100%;
	user-select: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
}
.polygonjs-app canvas.active {
	cursor: pointer;
}
`;

interface StylesheetTagData {
	id: string;
	content: string;
}
interface ScriptTagData {
	id: string;
	content: string;
	type?: 'application/javascript' | 'module';
}

export interface PreloadLinkTagOptions {
	as?: string;
	crossorigin?: boolean;
}
interface TagsOptions {
	autoPlay: boolean;
}

const DEFAULT_TITLE = 'a WebGL Experience created with @polygonjs';
export class HTMLHeadApi {
	private _stylesheetContentById: Map<string, string> = new Map();
	private _scriptDataById: Map<string, ScriptTagData> = new Map();
	private _preloadUrls: Map<string, PreloadLinkTagOptions> = new Map();

	constructor(protected htmlApi: HTMLApi) {
		this.addStylesheet({id: 'polygonjs-default', content: CSS_BASE});
	}

	/*
	 *
	 * OPTIONS
	 *
	 */
	private _title: string = DEFAULT_TITLE;
	private _description: string = DEFAULT_TITLE;
	private _twitter: string = '@polygonjs';
	private _siteUrl: string = 'https://polygonjs.com';
	private _autoPlay: boolean = true;
	setTitle(title: string) {
		this._title = title;
	}
	title() {
		return this._title;
	}
	setDescription(description: string) {
		this._description = description;
	}
	description() {
		return this._description;
	}
	setSiteUrl(url: string) {
		this._siteUrl = url;
	}
	siteUrl() {
		return this._siteUrl;
	}
	setTwitter(twitter: string) {
		this._twitter = twitter;
	}
	twitter() {
		return this._twitter;
	}
	setAutoPlay(autoPlay: boolean) {
		this._autoPlay = autoPlay;
	}

	/*
	 *
	 * ALL
	 *
	 */
	tags(): string {
		return [this.metaTags(), this.titleTag(), this.stylesheets(), this.scripts(), this.preloadLinks()].join('\n');
	}

	/*
	 *
	 * TITLE
	 *
	 */
	// setTitle(title: string) {
	// 	this._title = title;
	// }
	titleTag(): string {
		return `<title>${this.title()}</title>`;
	}

	/*
	 *
	 * STYLE
	 *
	 */
	addStylesheet(data: StylesheetTagData) {
		const existingData = this._stylesheetContentById.get(data.id);
		if (existingData) {
			console.error(`stylesheet with id '${data.id}' already given`);
			return;
		}
		this._stylesheetContentById.set(data.id, data.content);
	}
	stylesheets(): string {
		const tags: string[] = [];
		this._stylesheetContentById.forEach((content, id) => {
			const tag = `
<style id="${id}">
	${content}
</style>`;
			tags.push(tag);
		});
		return tags.join('\n');
	}

	/*
	 *
	 * JAVASCRIPT TAGS
	 *
	 */
	addScript(data: ScriptTagData) {
		const existingData = this._scriptDataById.get(data.id);
		if (existingData) {
			console.error(`script with id '${data.id}' already given`);
			return;
		}
		this._scriptDataById.set(data.id, data);
	}
	private _scriptTags(): string[] {
		const tags: string[] = [];
		this._scriptDataById.forEach((data, id) => {
			const tag = this._scriptTag(data, {autoPlay: this._autoPlay});
			tags.push(tag);
		});
		return tags;
	}
	scripts(): string {
		return this._scriptTags().join('\n');
	}

	/*
	 *
	 * PRELOAD LINKS
	 *
	 */
	addPreloadUrl(url: string, options?: PreloadLinkTagOptions) {
		this._preloadUrls.set(url, options || {});
	}
	preloadLinks(): string {
		const tags: string[] = [];
		this._preloadUrls.forEach((options, url) => {
			const tag = HTMLHeadApi.preloadLinkTagFromUrl(url, options);
			tags.push(tag);
		});
		return tags.join('\n');
	}

	/*
	 *
	 * METATAGS
	 *
	 */
	private _metaTagStrings: string[] = [];
	metaTags() {
		const tags: string[] = [];
		for (let metaTag of this._metaTagStrings) {
			tags.push(metaTag);
		}
		return tags.join('\n');
	}

	addMetaTag(metaTagString: string) {
		this._metaTagStrings.push(metaTagString);
	}

	/*
	 *
	 * UTILS
	 *
	 */
	updateCurrentPage() {
		const head = document.getElementsByTagName('head')[0];
		this._scriptDataById.forEach((data, id) => {
			const scriptTag = document.createElement('script');
			// when using innerText, if there are multiple lines in the content,
			// this will insert <br> tags in between each lines.
			// And using innerHTML seems to behave as expected
			scriptTag.innerHTML = data.content;
			scriptTag.id = id;
			if (data.type) {
				scriptTag.type = data.type;
			}
			head.appendChild(scriptTag);
		});
	}
	insertIntoHTML(template: string) {
		const lines = template.split('\n');
		const newLines: string[] = [];
		for (let line of lines) {
			if (line.includes('</head>')) {
				this._scriptDataById.forEach((data, id) => {
					const tag = this._scriptTag(data);
					newLines.push(tag);
				});
			}
			newLines.push(line);
		}
		return newLines.join('\n');
	}
	private _scriptTag(data: ScriptTagData, options?: TagsOptions) {
		const attributes: string[] = [];
		attributes.push(`id="${data.id}"`);
		if (data.type) {
			attributes.push(`type="${data.type}"`);
		}
		const content = options ? _template(data.content)(options) : data.content;
		return `
<script ${attributes.join(' ')}>
	${content}
</script>`;
	}

	static preloadLinkTagFromUrl(url: string, options?: PreloadLinkTagOptions): string {
		const crossorigin = options?.crossorigin || false;
		const urlElements = url.split('?');
		const extElements = urlElements[0].split('.');
		const ext = extElements[extElements.length - 1].toLowerCase();
		let asAttribute = options?.as;

		if (asAttribute == null) {
			asAttribute = 'fetch';

			if (IMAGE_EXTENSIONS.includes(ext)) {
				asAttribute = 'image';
			}
			if (VIDEO_EXTENSIONS.includes(ext)) {
				asAttribute = 'video';
			}
			if (JS_EXTENSIONS.includes(ext)) {
				asAttribute = 'script';
			}
		}

		const crossoriginAttr = crossorigin ? 'crossorigin' : '';
		return `<link rel="preload" href="${url}" as="${asAttribute}" ${crossoriginAttr}/>`;
	}
}
