import {EditorApi} from '../EditorApi';

export class RemoteApi {
	public readonly ngrok = new NGrok();
	constructor(public readonly editorApi: EditorApi) {}
}

class NGrok {
	private _authToken?: string;
	private _closeExistingTunnelsOnCreate: boolean = true;
	setAuthToken(authToken?: string) {
		this._authToken = authToken;
	}
	authToken() {
		return this._authToken;
	}
	setCloseExistingTunnelsOnCreate(state: boolean) {
		this._closeExistingTunnelsOnCreate = state;
	}
	closeExistingTunnelsOnCreate() {
		return this._closeExistingTunnelsOnCreate;
	}
}
