import {EditorApi} from '../EditorApi';

export interface PostExportCallbackArgs {
	exportFolder: string;
	sceneName: string;
}
export interface PostExportCallbackReturnValue {
	command: string;
	publicPath: string;
}
type PostExportCallback = (args: PostExportCallbackArgs) => PostExportCallbackReturnValue;

export class ExportApi {
	constructor(public readonly editorApi: EditorApi) {}
	private _postExportCallback: PostExportCallback | undefined;
	setPostExportCommand(callback: PostExportCallback) {
		this._postExportCallback = callback;
	}
	postExportCommand(args: PostExportCallbackArgs) {
		if (this._postExportCallback) {
			return this._postExportCallback(args);
		}
	}
	private _exportFolder: string | undefined;
	setExportFolder(exportFolder: string) {
		this._exportFolder = exportFolder;
	}
	exportFolder() {
		return this._exportFolder;
	}
}
