import {PanelApi} from './panel/Panel';
import {HTMLApi} from './html/HTML';
import {ExportApi} from './export/Export';
import {MultiPlayerApi} from './multiplayer/MultiPlayer';

export class EditorApi {
	public readonly html = new HTMLApi(this);
	public readonly panel = new PanelApi(this);
	public readonly export = new ExportApi(this);
	public readonly multiplayer = new MultiPlayerApi(this);

	private _projectName: string | undefined;
	setProjectName(sceneName: string) {
		this._projectName = sceneName;
	}
	projectName() {
		return this._projectName;
	}
}

// used in configureEditor
export interface EditorApiOptions {
	api: EditorApi;
	sceneName: string;
}

export type ConfigureEditor = (options: EditorApiOptions) => void;
