import {HTMLApi} from './HTML';

interface BaseProgressBarTemplates {
	HTML: string;
	JS: string;
	CSS: string;
}

interface BaseProgressBar {
	templates: (options: any) => BaseProgressBarTemplates;
}

export class HTMLViewerApi {
	private _progressBar: BaseProgressBar | undefined;
	constructor(protected htmlApi: HTMLApi) {}

	setProgressBar(progressBar: BaseProgressBar | undefined) {
		this._progressBar = progressBar;
	}
	progressBar(): BaseProgressBar | undefined {
		return this._progressBar;
	}
}
