import {HTMLApi} from './HTML';

interface TagData {
	id: string;
	content: string;
}

type PolygonjsIdCallback = (sceneName: string) => void;

export class HTMLBodyApi {
	private _tagsByIdByGroup: Map<string, Map<string, TagData>> = new Map();
	constructor(protected htmlApi: HTMLApi) {}

	addTag(groupName: string, data: TagData) {
		let groupMap = this._tagsByIdByGroup.get(groupName);
		if (!groupMap) {
			groupMap = new Map();
			this._tagsByIdByGroup.set(groupName, groupMap);
		}
		groupMap.set(data.id, data);
	}
	tags(groupName: string): string {
		const tags: string[] = [];
		const groupMap = this._tagsByIdByGroup.get(groupName);
		if (groupMap) {
			groupMap.forEach((data, id) => {
				tags.push(data.content);
			});
		}
		return tags.join('\n');
	}

	private _polygonjsIdFunction: PolygonjsIdCallback = (sceneName: string) => {
		return `polygonjs-app-${sceneName}`;
	};
	polygonjsId(sceneName: string) {
		return this._polygonjsIdFunction(sceneName);
	}
	setPolygonjsIdCallback(callback: PolygonjsIdCallback) {
		this._polygonjsIdFunction = callback;
	}
}
