import {PanelApi} from './Panel';

export class PanelCustomApi {
	private _HTMLByPanelName: Map<string, string> = new Map();
	constructor(public readonly panelApi: PanelApi) {
		// this._html_by_panel_name.set('bayer1', '<a href="https://polygonjs.com">This is a link</a>');
		// this._html_by_panel_name.set('bayer2', ``);
	}

	addPanel(panelName: string, content: string) {
		this._HTMLByPanelName.set(panelName, content);
	}
	panelNames() {
		let list: string[] = [];
		this._HTMLByPanelName.forEach((panel, name) => {
			list.push(name);
		});
		list = list.sort();
		return list;
	}
	contentForPanel(panel_name: string) {
		return this._HTMLByPanelName.get(panel_name);
	}
}
